.freeAnswer {
  min-height: 30svh;
  border-radius: 10px;
  padding: 5px 20px;
  font-size: 16px;
}
.finalModalSection {
  display: flex;
  justify-content: space-between;
  gap: 10px;
}

.secrionLeft,
.sectionRight {
  max-width: 500px;
}

.sectionRight {
  display: flex;
  justify-content: center;
  align-items: center;
}

.freeAnswerSection {
  display: flex;
  flex-direction: column;
  gap: 20px;
}

.freeAnswerSection input {
  height: 40px;
  border-radius: 10px;
  border: 1px solid black;
  padding: 5px 20px;
  font-size: 16px;
}

@keyframes shake {
  0% {
    transform: scale(1);
  }
  20% {
    transform: scale(1.02);
  }
  40% {
    transform: scale(1);
  }
  60% {
    transform: scale(1.02);
  }
  80% {
    transform: scale(1);
  }
  100% {
    transform: scale(1.02);
  }
}

@media (max-width: 900px) {
  .finalModalSection {
    flex-direction: column-reverse;
  }
}
