.mainInstrumental {
  display: flex;
  justify-content: center;
  gap: 100px;
}
.wrapperInstrumentalVariants {
  display: flex;
  flex-direction: column;
  gap: 20px;
}
.instrumentalType {
  width: 200px;
}
.instrumentalAnswers {
  margin-right: auto;
}
.instrumentalVariants {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.instrumentalCase {
  cursor: pointer;
  padding: 10px 20px;
  border-radius: 10px;
  border: 1px solid black;
  margin-top: 10px;
  text-align: center;
  transition: 0.4s;
}
.instrumentalCase:hover {
  transform: scale(1, 1.1);
}
.instrumentalDescription {
  text-align: center;
}
.instrumentalDescription p {
  max-width: 750px;
  text-align: left;
}
.elseInstrumental {
  cursor: pointer;
  padding: 5px 10px;
  border-radius: 10px;
  border: 1px solid black;
  transition: 0.4s;
}
.elseInstrumental:hover {
  transform: scale(1, 1.1);
}
.imgWrapper {
  position: absolute;
  border: 10px solid white;
  z-index: 11;
  display: flex;
  justify-content: center;
  align-items: center;
}
.showInstrumentalImage {
  border: 1px solid black;
  padding: 3px 5px;
  text-align: center;
  border-radius: 5px;
  cursor: pointer;
}
@media (max-width: 1250px) {
  .imgWrapper {
    transform: rotate(-90deg);
  }
  .imgWrapper img {
    max-height: 28vh;
  }
}
@media (max-width: 750px) {
  .mainInstrumental {
    gap: 50px;
  }
  .instrumentalCase:hover {
    transform: none;
  }
  .elseInstrumental:hover {
    transform: none;
  }
  .instrumentalVariants div,
  .instrumentalAnswers div {
    font-size: 14px;
  }
}
@media (max-width: 540px) {
  .instrumentalType {
    width: 150px;
  }
}
@media (max-width: 390px) {
  .instrumentalType {
    width: 130px;
  }
}
