.textContainer {
  width: 250px;
  height: max-content;
  padding: 5px 10px;
  border-radius: 5px;
  background: var(--substrate);
  color: black;
  top: 0;
  z-index: 2;
  transition: 0.4s;
}
.hover:hover {
  transform: scale(1.1);
  transition: 0.4s;
}

.doctorTalkInput,
.patientTalkInput {
  position: absolute;
  background: var(--talk-color);
  color: white;
  width: 250px;
  padding: 7px 5px 10px 7px;
}
.patientTalkInput:before {
  content: "";
  position: absolute;
  right: 100%;
  top: 5px;
  width: 0;
  height: 0;
  border-top: 26px solid transparent;
  border-right: 28px solid var(--talk-color);
  border-bottom: -5px solid transparent;
}
.doctorTalkInput:before {
  content: "";
  position: absolute;
  left: 100%;
  top: 5px;
  width: 0;
  height: 0;
  border-top: 26px solid transparent;
  border-left: 28px solid var(--talk-color);
  border-bottom: -5px solid transparent;
}
.doctorTalkInput {
  right: calc(var(--index) * 27);
  top: -2vh;
}
.patientTalkInput {
  left: calc(var(--index) * 30);
  top: 5vh;
}

@keyframes typing {
  0% {
    width: 0;
    overflow: hidden;
  }
  100% {
    width: 50px;
  }
}

@media (max-width: 1500px) {
  .doctorTalkInput,
  .patientTalkInput {
    width: 200px;
  }
}

@media (max-width: 750px) {
  .textContainer {
    width: auto;
  }
  .typing-demo {
    font-size: 16px;
  }
  .hover:hover {
    transform: scale(1);
  }
  .doctorTalkInput,
  .patientTalkInput {
    width: 200px;
  }
}
@media (max-width: 700px) {
  .doctorTalkInput {
    top: 5px;
  }
}
@media (max-width: 500px) {
  .doctorTalkInput,
  .patientTalkInput {
    width: 140px;
  }
  .doctorTalkInput {
    right: calc(var(--index) * 21);
  }
  .patientTalkInput {
    left: calc(var(--index) * 24);
  }
  .typing-demo {
    font-size: 13px;
  }
  .patientTalkInput:before {
    border-top: 16px solid transparent;
    border-right: 13px solid var(--talk-color);
  }
  .doctorTalkInput:before {
    border-top: 16px solid transparent;
    border-left: 13px solid var(--talk-color);
  }
}
@media (max-width: 430px) {
  .doctorTalkInput,
  .patientTalkInput {
    width: 130px;
  }
  
}
