.pullQuestionsWrapper {
  position: absolute;
  right: 50px;
  bottom: 30px;
  background: var(--content-wrapper);
  border-radius: 10px;
  padding: 0 10px 10px 10px;
  opacity: 0.85;
}
.pullQuestionsWrapper h2 {
  text-align: center;
  color: black;
  margin-bottom: 10px;
  background: var(--substrate);
  border-radius: 5px;
  margin-top: 5px;
  font-size: 28px;
}
.pullQuestions {
  display: flex;
  flex-direction: column;
  gap: 5px;
  font-size: 20px;
}
.question {
  cursor: pointer;
}
.question .textContainer {
  width: 25vw;
}
.clouseQuestionsAndNext {
  cursor: pointer;
  position: absolute;
  right: 0;
  left: 0;
  margin: 0 auto;
  top: -40px;
  width: 25vw;
  border: none;
  font-size: 20px;
  background: var(--button-active);
  border-radius: 10px;
  padding: 5px 10px;
  animation: puls 0.9s linear infinite;
}
@keyframes puls {
  0% {
    transform: scale(1, 1);
  }
  50% {
    transform: scale(1.1, 1.1);
  }
  100% {
    transform: scale(1, 1);
  }
}
@media (max-width: 1560px) {
  .pullQuestionsWrapper {
    right: 0;
  }
}
@media (max-width: 1150px) {
  .pullQuestionsWrapper {
    margin: 0 auto;
    left: 0;
    width: max-content;
  }
  .question .textContainer {
    width: auto;
  }
  .clouseQuestionsAndNext {
    width: 90%;
    animation: puls 0.9s linear infinite;
  }
}
@media (max-width: 750px) {
  .pullQuestionsWrapper {
    bottom: 0;
    width: 100vw;
    border-radius: 0;
    padding: 5px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    /* margin-right: -5px; */
  }
  .pullQuestions {
    width: 95vw;
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 8px;
  }
  .pullQuestionsWrapper h2 {
    font-size: 20px;
    margin-bottom: 10px;
    width: 95vw;
  }
  .question {
    text-align: center;
    width: 100%;
  }
}
