.opacityFone {
  width: 100vw;
  min-height: 100svh;
  background-color: rgba(0, 0, 0, 0.5);
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
  z-index: 15;
}

.sendButton {
  margin-top: 5vh;
  padding: 10px;
  border: none;
  border-radius: 5px;
  background-color: rgb(0, 162, 255);
  cursor: pointer;
  font-size: 20px;
  font-weight: 900;
  color: black;
  letter-spacing: 0.08em;
}
.sendButtonAbsolutle {
  position: absolute;
  bottom: 30px;
  left: 30px;
}

.modalContent {
  max-width: 95%;
  padding: 10px 30px 50px 30px;
  background-color: var(--content-wrapper);
  /* color: black; */
  border-radius: 30px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  position: relative;
  height: max-content;
  z-index: 6;
}

.alertWrapper {
  position: absolute;
  height: 100%;
  width: 100%;
  background-color: var(--content-wrapper);
  left: 0;
  bottom: 0;
  border-radius: 30px;
}

.alertModal {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  min-height: 60%;
}

.startButton {
  padding: 10px 30px;
  font-size: 24px;
  border: none;
  box-shadow: 0 0 10px black;
  border-radius: 10em;
  cursor: pointer;
  margin-top: 70px;
}
.diagnosisContent {
  display: flex;
  gap: 50px;
  align-items: baseline;
  justify-content: space-around;
  margin-bottom: auto;
}
.charter {
  cursor: pointer;
  width: max-content;
  padding: 0 15px;
  height: 40px;
  border-radius: 10px;
  transition: 0.5;
  opacity: 0;
  animation: showElement 0.6s linear forwards;
}
@keyframes showElement {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
    transform: translate(0, 0);
  }
}
.showElement {
  opacity: 1;
}
.variants {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  grid-template-rows: 1fr 1fr 1fr;
  gap: 10px;
}

.variants span {
  position: relative;
  border: 1px solid white;
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
  padding: 2px 5px;
  border-radius: 10px;
  cursor: pointer;
  font-size: 14px;
  min-height: 50px;
}
@keyframes opacity {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

@keyframes welcomeMedicalGroup {
  0% {
  }
  100% {
    opacity: 1;
    transform: translate(0 0);
  }
}
@keyframes welcomeMedCurrent {
  0% {
  }
  100% {
    opacity: 1;
  }
}
.pulsCharter {
  animation: pulsCharter 0.6s linear infinite;
}
.showModalHint {
  position: absolute;
  z-index: 20;
  padding: 20px;
  background: black;
  border-radius: 10px;
  border: 1px solid white;
  opacity: 0;
  animation: opacity 0.5s linear forwards;
}
.showModalHint div:nth-child(1) {
  position: absolute;
  top: 10px;
  right: 10px;
  cursor: pointer;
}
.showModalHint div:nth-child(4) {
  min-height: 70vh;
  overflow-y: scroll;
}
.showModalHint div:nth-child(4)::-webkit-scrollbar {
  width: 0;
}
@keyframes opacity {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}
@keyframes pulsCharter {
  0% {
    transform: scale(1, 1);
  }
  50% {
    transform: scale(1, 1);
  }
  60% {
    transform: scale(1.1, 1.1);
  }
  70% {
    transform: scale(1, 1);
  }
  80% {
    transform: scale(1.1, 1.1);
  }
  90% {
    transform: scale(1, 1);
  }
  100% {
    transform: scale(1, 1);
  }
}

.diagnosisVariantContainer {
  display: flex;
  flex-direction: column;
  gap: 10px;
}

.diagnosisVariant {
  position: relative;
  cursor: pointer;
  padding: 15px 20px;
  max-width: 300px;
  border: 1px solid black;
  border-radius: 10px;
}

.clouseDiv {
  width: max-content;
  margin-left: auto;
  margin-bottom: auto;
  font-size: 30px;
  cursor: pointer;
  padding-bottom: 20px;
}
.treatmentVariantsContainer {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 10px;
}
.treatment-variant {
  position: relative;
  width: 90%;
  padding: 5px 10px;
  text-align: center;
  margin: 0 auto;
  border: 1px solid black;
  height: 100px;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 10px;
  cursor: pointer;
}
.clouseVariant {
  position: absolute;
  top: 5%;
  right: 3%;
}
@media (max-width: 1500px) {
}

@media (max-width: 1000px) {
  .charter {
    display: flex;
    justify-content: center;
    align-items: center;
  }
  .charter h2 {
    font-size: 18px;
  }
  .diagnosisContent {
    gap: 20px;
  }
}
@media (max-width: 790px) {
  .diagnosisContent {
    flex-direction: column;
    justify-content: center;
  }
  .diagnosisContent h4 {
    margin: 10px;
  }
  .charter h2 {
    margin: 0;
  }
  .charterWrapper {
    display: flex;
  }
}

@media (max-width: 700px) {
  .treatment-variant span:nth-child(1) {
    font-size: 14px;
  }
  .modalContent {
    padding: 10px 10px 30px 10px;
    min-width: 95vw;
  }
  .treatmentFlex {
    justify-content: space-between;
  }
  .modalContent h2 {
    font-size: 18px;
  }
  .modalContent li {
    font-size: 14px;
  }
  .charterWrapper {
    display: flex;
    flex-direction: column;
  }
  .diagnosisContent {
    min-height: 90vh;
    height: max-content;
  }
  .variants {
    grid-template-columns: 1fr 1fr;
  }
}
@media (max-width: 450px) {
  .diagnosisContent {
    gap: 0;
  }
  .variants span {
    padding: 1px 2px;
    font-size: 14px;
  }
  .variants {
    gap: 3px;
  }
}
@media (max-width: 370px) {
  .variants span {
    font-size: 13px;
    max-width: 160px;
  }
  .variants {
    gap: 2px;
  }
  .currentMed span {
    max-width: 140px;
  }
}
@media (max-width: 320px) {
  .modalContent {
    padding: 15px 2px;
    width: 98vw;
  }
  .variants {
    grid-template-columns: 1fr;
    max-height: 230px;
    overflow-y: scroll;
    margin-bottom: 40px;
  }
  .variants::-webkit-scrollbar {
    width: 0;
  }
  .variants span {
    font-size: 13px;
    max-width: none;
  }
  .charter span {
    font-size: 12px;
  }
}
