.App {
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 100svh;
  max-height: max-content;
  gap: 80px;
  position: relative;
  overflow-x: hidden;
  background-image: url("./components/Image/doctor_room.jpg");
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  color: white;
 
}

:root {
  --index: 1vw + 1vh;
  --content-wrapper: rgb(53, 44, 44);
  --substrate: white;
  --button-active: white;
  --talk-color: #00000080;
  --content-wrapper-small-screen: rgba(255, 255, 255, 0.85);
  --menu-buttons-disabled-color: white;
}

.menu-step-button {
  margin-top: 5px;
}
.step-result {
  border: 1px solid black !important;
}
.step-result:disabled {
  border: none !important;
}
.menuButtons {
  position: absolute;
  background: var(--content-wrapper);
  border-radius: 10px;
  padding: 10px;
  display: flex;
  flex-direction: column;
  top: 50px;
  left: 50px;
  gap: 10px;
  z-index: 2;
}
.hint {
  position: absolute;
  top: -25px;
  right: -25px;
  font-size: 18px;
  width: 40px;
  height: 40px;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 50%;
  background: var(--substrate);
  cursor: pointer;
  color: black;
}
.hintInfo {
  position: absolute;
  top: 50px;
  background-color: #252525;
  padding: 5px 10px;
  border-radius: 5px;
  color: white;
  z-index: 15;
}
.menuButtons button {
  cursor: pointer;
  border: none;
  font-size: 24px;
  padding: 4px 10px;
  background: var(--button-active);
  color: black;
  border-radius: 5px;
  transition: 0.4s;
}
.menuButtons button:hover {
  transform: scale(1.1);
  transition: 0.4s;
}
.menuButtons button:disabled {
  color: var(--menu-buttons-disabled-color);
  background: transparent;
  transform: scale(1);
}
.dialogInputs {
  position: absolute;
  top: 0;
}

.doctorPerson {
  width: max-content;
  position: absolute;
  bottom: 1vh;
  margin-left: 25vw;
}
.doctorPerson img {
  width: calc(var(--index) * 37);
}
.patientPerson {
  width: max-content;
  position: absolute;
  bottom: 0;
  margin-right: 35vw;
  
}
.patientPerson img {
  width: calc(var(--index) * 36);
}
.dialogs {
  position: absolute;
  background-color: #252525c2;
  border-radius: 10px;
  display: flex;
  flex-direction: column;
  width: 350px;
  height: 95svh;
  transition: 1s;
  z-index: 5;
}
.dialogsContent {
  overflow-y: scroll;
  padding-right: 15px;
  padding-left: 15px;
  padding-top: 15px;
}
.dialogsContent::-webkit-scrollbar {
  width: 5px;
}

.dialogsContent::-webkit-scrollbar-track {
  -webkit-box-shadow: 5px 5px 5px -5px rgba(34, 60, 80, 0.2) inset;
  background-color: #f9f9fd;
  border-radius: 10px;
}

.dialogsContent::-webkit-scrollbar-thumb {
  border-radius: 10px;
  background: linear-gradient(180deg, #d9e836, #8a960e);
}
.openDialogsButton {
  position: absolute;
  color: white;
  border: none;
  background-color: transparent;
  font-size: 24px;
  cursor: pointer;
}
.poweredByMeroPlace {
  text-align: center;
  background-color: #252525;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 10px;
  border-radius: 10px;
  margin-top: 20px;
}
.poweredByMeroPlace img {
  width: 90px;
}

.openMainMenu {
  display: none;
}
@keyframes talk {
  0% {
    height: 0;
  }
  100% {
    height: 10px;
  }
}
.pulsHint {
  animation: pulsHint 1s linear infinite;
}
@keyframes pulsHint {
  0% {
    transform: scale(1, 1);
    box-shadow: 0 0 0px red;
  }
  25% {
    transform: scale(1.1, 1.1);
    box-shadow: 0 0 7px red;
  }
  50% {
    transform: scale(1, 1);
    box-shadow: 0 0 0px red;
  }
  75% {
    transform: scale(1.1, 1.1);
    box-shadow: 0 0 7px red;
  }
  100% {
    transform: scale(1, 1);
    box-shadow: 0 0 0px red;
  }
}
@media (max-width: 1700px) {
  .menuButtons {
    opacity: 0.85;
  }
}
@media (max-width: 1560px) {
  .menuButtons {
    left: 0;
  }
}
@media (max-width: 1500px) {
  .menuButtons button {
    font-size: 18px;
  }
}
@media (max-width: 1150px) {
  .menuButtons {
    gap: 5px;
    top: 30px;
  }
  .poweredByMeroPlace {
    margin-top: 5px;
  }
  .doctorPerson {
    margin-left: 45vw;
  }
  .patientPerson {
    margin-right: 45vw;
  }
}
@media (max-width: 800px) {
  .menuButtons {
    gap: 0;
  }
  .poweredByMeroPlace {
    font-size: 12px;
  }
}
@media (max-width: 750px) {
  .dialogs {
    background-color: #252525e8;
    z-index: 15;
    width: 98vw;
  }
  .menuButtons button:hover {
    transform: scale(1);
  }
  .doctorPerson {
    margin-left: 0;
    right: 0;
  }
  .patientPerson {
    margin-right: 0;
    left: 0;
  }

  .hint {
    right: -50px;
  }
  .menuButtons {
    z-index: 10;
    /* background: var(--substrate); */
    transition: 0.7s;
    opacity: 1;
  }
  .openMainMenu {
    display: block;
    position: absolute;
    top: 35px;
    color: black;
    background: var(--substrate);
    padding: 5px 10px;
    font-size: 18px;
    font-weight: 700;
    border-radius: 10px;
    cursor: pointer;
  }
}
@media (max-width: 600px) {
  .doctorPerson img {
    width: calc(var(--index) * 32);
  }
  .patientPerson img {
    width: calc(var(--index) * 31);
  }
  .doctorPerson {
    margin-left: 0;
    right: -2vw;
    bottom: 7vh;
  }
  .patientPerson {
    margin-right: 0;
    left: -5vw;
    bottom: 7vh;
  }
}
@media (max-width: 430px) {
  .doctorPerson {
    right: -4vw;
  }
  .patientPerson {
    left: -7vw;
  }
}
@media (max-width: 370px) {
  .menuButtons button {
    font-size: 15.5px;
  }
}
